import { compareVersion } from './compareVersion';
import { getCurrentAppVersion } from './getCurrentAppVersion';

/**
 * 判断当前版本是否大于某个版本号
 * @param version 需要比较的版本号
 * @returns true：大于版本号，false：小于等于
 */
export function currentGreaterThanVersion(version) {
    const currentVersion = getCurrentAppVersion();
    return compareVersion(currentVersion, version) === 1;
}
